<template>
  <div class="saleCard">
    <header>
      <span>维护卡剩余数量</span>
      <span class="count">{{sale.pay_currency}}</span>
    </header>
    <p>对方ID号</p>
    <div class="ID">
      <input type="text" placeholder="请输入对方的ID号码" v-model="salerID" />
      <i></i>
    </div>
    <div class="tips">
      <i></i>
      <span>
        请
        <span class="red">仔细核对对方账户信息</span> ，确认无误后点击卖出
      </span>
    </div>
    <p>卖出数量</p>
    <input type="text" placeholder="请输入卖出的数量" class="sale_num" v-model="salerNum" />
    <div class="button" @click="saleNow">立即卖出</div>
  </div>
</template>

<script>
import axios from "axios";
import url from "@/axios/api.js";
export default {
  data() {
    return {
      sale: [],
      salerID: "",
      salerNum: ""
    };
  },
  created() {
    this.getData();
  },
  methods: {
    getData() {
      let params = new URLSearchParams();
      params.append("openid", localStorage.getItem("openid"));
      params.append("type", this.$route.query.type);
      axios.post(url.saleCard, params).then(res => {
        this.sale = res.data.data;
      });
    },
    saleNow() {
      let params = new URLSearchParams();
      params.append("openid", localStorage.getItem("openid"));
      params.append("user_no", this.salerID);
      params.append("pay_currency", this.salerNum);
      axios.post(url.saleCardnow, params).then(res => {
        this.$router.go(-1);
      });
    }
  }
};
</script>

<style scoped lang="scss">
input {
  outline: none;
  border: none;
}
.saleCard {
  padding: 10px;
  p {
    margin: 20px 0;
  }
  header {
    height: 40px;
    padding: 10px;
    display: flex;
    align-items: center;
    background: linear-gradient(
      90deg,
      rgba(35, 149, 255, 1),
      rgba(35, 201, 255, 1)
    );
    box-shadow: 0px 3px 5px 0px rgba(35, 149, 255, 0.4);
    color: #fff;
    border-radius: 5px;
    margin-bottom: 10px;
    .count {
      margin-left: auto;
    }
  }
  .ID {
    display: flex;
    border-bottom: 1px solid rgba(229, 229, 229, 1);
    margin: 10px 0;
    padding-bottom: 10px;
    :-ms-input-placeholder {
      font-size: 14px;
      color: rgba(158, 158, 158, 1);
    }
    i {
      display: block;
      background: url(../../assets/images/card_scanning@2x.png) no-repeat;
      background-size: 100% 100%;
      height: 20px;
      width: 20px;
      margin-left: auto;
    }
  }
  .tips {
    height: 40px;
    display: flex;
    align-items: center;
    background: url(../../assets/images/card_prompt_bg@2x.png) no-repeat;
    background-size: 100% 100%;
    padding: 8px 10px 0 10px;
    font-size: 12px;
    margin-bottom: 20px;
    i {
      display: block;
      background: url(../../assets/images/card_prompt@2x.png) no-repeat;
      background-size: 100% 100%;
      width: 14px;
      height: 14px;
      margin-right: 5px;
    }
    .red {
      color: #ff4c23;
    }
  }
  :-ms-input-placeholder {
    font-size: 16px;
    color: rgba(166, 166, 166, 1);
  }
  .sale_num {
    width: 100%;
    padding-bottom: 10px;
    border-bottom: 1px solid rgba(239, 44, 16, 1);
    margin: 20px 0;
  }
  .button {
    padding: 15px 0;
    background: rgba(255, 76, 35, 1);
    box-shadow: 0px 3px 5px 0px rgba(255, 112, 54, 0.4);
    border-radius: 23px;
    text-align: center;
    color: #fff;
  }
}
</style>



